import Highcharts from 'highcharts';
import debounce from 'lodash/debounce';

export default {
    template: `<div class='chart'></div>`,
    props: {
        options: {
            type: Object,
            default: () => ({})
        },
        series: {
            type: Array,
            default: () => []
        }
    },
    data() {
        return {}
    },
    watch: {
        options: {
            handler(value) {
                if (this.chart) {
                    this.chart.update(value);
                }
            },
            deep: true
        },
        series: {
            handler() {
                if (this.chart) {
                    this.seriesWatcher();
                }
            },
            deep: true
        }
    },
    methods: {
        init() {
            if (this.chart) {
                return;
            }
            this.chart = new Highcharts.chart(this.$el, {
                ...this.options,
                series: this.series
            });
        },
        seriesWatcher: debounce(function() {
            this.chart.series.forEach((s, i) => {
                s.setData(this.series[i].data, false);
            });
            this.chart.redraw();
        }, 100)
    },
    mounted() {
        this.init();
    },
    beforeDestroy() {
        if (this.chart) {
            this.chart.destroy();
        }
    }
}



