'use strict';

// import Vue from 'vue/dist/vue.common';
import noUiSlider from 'nouislider';

export default {
    // name: 'RangeInput',
    template: '#range-input-template',
    props: {
        min: {
            type: Number,
            required: true
        },
        max: {
            type: Number,
            required: true
        },
        range: {
            type: Object,
            default() {
                return {
                    min: [this.min],
                    max: [this.max]
                };
            }
        },
        step: {
            type: Number,
            required: true
        },
        value: {
            type: Number,
            default: null
        },
        name: {
            type: String
        }
    },
    data() {
        return {}
    },
    computed: {
        plugin() {
            return this.$refs.slider.noUiSlider;
        },
        inputStyleObj() {
            return {
                backgroundSize: (this.value - this.min) * 100 / (this.max - this.min) + '% 100%'
            };
        }
    },
    methods: {
        updateValue(value) {
            this.$emit('input', Number(value));
        },
    },

    watch: {
        value() {
            // console.log(this.value, this.pluginValue(), this.pluginValue() === this.value);
            if (this.plugin.get() !== this.value) {
                this.plugin.set(this.value);
            }
        },
        range() {
            this.plugin.updateOptions({ range: this.range });
        },
        step() {
            this.plugin.updateOptions({ step: this.step });
        }
    }
    ,
    mounted() {
        noUiSlider.create(this.$refs.slider, {
            animate: false,
            start: this.value,
            connect: 'lower',
            step: this.step,
            range: this.range,
            format: { to: Math.round, from: Math.round }
        });

        this.$refs.slider.noUiSlider.on('update', (values, handle) => {
            // console.log('to: ', values[handle]);
            this.updateValue(values[handle]);
        });
    },
    beforeDestroy() {
        this.$refs.slider.noUiSlider.destroy();
    }
}



