'use strict';

import Vue from 'vue';

import RangeInput from '../range-input/range-input';
import Chart from '../chart/chart';
import chartOptions from './data';
// window.Vue = Vue;

export default function () {

    new Vue({
        el: '#calculator',
        data: {
            controls: {
                incomePlan: {
                    min: 10000,
                    max: 3000000,
                    step: 10000,
                    makeRange() {
                        return {
                            min: [this.min, 10000],
                            '25%': [100000, 100000],
                            max: [this.max]
                        }
                    },
                    value: 300000
                },
                period: {
                    min: 1,
                    max: 10,
                    step: 1,
                    value: 3
                },
                annualYield: {
                    min: 1,
                    max: 100,
                    step: 1,
                    value: 18
                }
            },
            chartOptions
        },
        methods: {
            makeSeriesArray(fn) {
                return [...Array(this.controls.period.value)].map(fn);
            },
            declOfNum(number, titles) {
                let cases = [2, 0, 1, 1, 1, 2];
                return titles[ (number%100>4 && number%100<20)? 2 : cases[(number%10<5)?number%10:5] ];
            }
        },
        computed: {
            investments() {
                return Math.ceil(this.controls.incomePlan.value / this.controls.annualYield.value * 100);
            },
            K() {
                return 1 + this.controls.annualYield.value / 100;
            },
            totalIncome() {
                return Math.round(this.investments * this.K ** this.controls.period.value);
            },
            series() {
                return [
                    {
                        name: 'Доход за данный период',
                        data: this.makeSeriesArray((x, i) => Math.round(this.investments * (this.K ** (i + 1) - this.K ** i)))
                    },
                    {
                        name: 'Доход за предыдущие годы',
                        data: this.makeSeriesArray((x, i) => Math.round(this.investments * (this.K ** i - 1)))
                    },
                    {
                        name: 'Начальные инвестиции',
                        data: this.makeSeriesArray(() => this.investments)
                    }
                ]
            }
        },
        mounted() {},
        filters: {
            formatPrice(num) {
                let decimal = 0,
                    separator = ' ',
                    decpoint = '.',
                    exp10 = Math.pow(10, decimal),
                    r = Math.round(parseFloat(num) * exp10) / exp10,
                    rr = Number(r).toFixed(decimal).toString().split('.'),
                    b = rr[0].replace(/(\d{1,3}(?=(\d{3})+(?:\.\d|\b)))/g, '$1' + separator);
                return r = rr[1] ? b + decpoint + rr[1] : b;
            }
        },
        components: {
            RangeInput,
            Chart
        }
    });

}



